nav {
  background-color: #4CAF50;
  padding: 10px;
  border: 2px solid #333; /* Adding a frame around the navbar */
  border-radius: 8px; /* Rounded corners for the frame */
  display: flex;
  justify-content: space-between; /* Space between brand and menu icon */
  overflow-x: auto; /* Adding horizontal scrollbar */
  white-space: nowrap; /* Prevent wrapping */
}

.menu-icon {
  display: none;
  cursor: pointer;
  font-size: 24px;
  color: white;
}

.nav-links {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-wrap: nowrap; /* Prevent wrapping */
}

.nav-links li {
  margin-left: 20px; /* Increase space between buttons */
}

.nav-links li a {
  color: white;
  text-decoration: none;
  padding: 8px 16px; /* Adding some padding for better spacing */
  border: 1px solid transparent; /* Adding transparent border for consistent sizing */
  border-radius: 4px; /* Rounded corners for buttons */
}

.nav-links li a:hover {
  background-color: #45a049; /* Slightly darker green on hover */
  border: 1px solid white; /* Adding border on hover */
}

@media (max-width: 768px) {
  .menu-icon {
    display: block; /* Show menu icon on smaller screens */
  }

  .nav-links {
    display: none; /* Hide nav links initially on smaller screens */
    flex-direction: column; /* Stack items vertically on smaller screens */
    width: 100%; /* Full width */
    position: absolute; /* Absolute positioning */
    top: 50px; /* Adjust based on navbar height */
    left: 0;
    background-color: #4CAF50;
    z-index: 1000;
  }

  .nav-links.open {
    display: flex; /* Show nav links when menu is open */
  }

  .nav-links li {
    margin: 10px 0; /* Add margin vertically between links */
  }

  .nav-links li a {
    text-align: center; /* Center the text */
    width: 100%; /* Full width links */
  }
}
